import {validatIp} from './helpers/index';

const ipInput = document.querySelector('.search-bar__input');
const btn = document.querySelector('button');

const ipInfo = document.querySelector('#ip');
const locationInfo = document.querySelector('#location');
const timezoneInfo = document.querySelector('#timezone');
const ispInfo = document.querySelector('#isp');


btn.addEventListener('click', getDate);
ipInput.addEventListener('keydown', handKey);

function getDate() {
    //Проверка данных
    if (validatIp(ipInput.value)) {
        fetch(`https://geo.ipify.org/api/v2/country,city?apiKey=at_JyxIPRlZNzlatCwgsUhKhXcfTW1fg&ipAddress=${ipInput.value}`)
            .then(response => response.json())
            .then(setInfo);
    }

    
}

function handKey(e) {
    if (e.key === 'Enter') {
        getDate();
    }
}

function setInfo(mapDate) {
    console.log(mapDate);
    ipInfo.innerText = mapDate.ip;
    locationInfo.innerText = mapDate.location.country + ' ' + mapDate.location.region;
    timezoneInfo.innerText = mapDate.location.timezone;
    ispInfo.innerText = mapDate.isp;
}